import React, { useState } from 'react';

export const HistoryContext = React.createContext({
  push: () => null,
  back: () => null,
  path: () => ''
});

const Router = props => {
  const { config, defaultPage } = props;
  const [route, setRoute] = useState(defaultPage);

  function History(index) {
    this.history = [index];
    this.params = {};

    this.back = function() {
      if (this.history.length > 1) {
        this.history.pop();
        setRoute(this.path());
      }
    };
    this.push = function(path) {
      this.history.push(path);
      setRoute(this.path());
    };

    this.reset = () => (this.history = [index]);
    this.path = () => this.history[this.history.length - 1];
  }

  const [history] = useState(new History(defaultPage));
  return (
    <HistoryContext.Provider value={history}>
      {config[route]}
      <div style={{
        position: "absolute",
        top: "0",
        left: "0",
        color: "white"
      }}>DEBUG: {route}</div>
    </HistoryContext.Provider>
  );
};

export default Router;
