import React, { useContext, useState } from 'react';
import { HistoryContext } from '../components/Router';
import Fade from '../components/Fade';
import { waitFor } from 'UtilAPI';
import ContentBox from 'components/ContentBox';
import backBtn from 'images/back-btn.png';
import seeBtn from 'images/see.png';
import config from 'config';
// 查看盒子
const Listen = () => {
  const history = useContext(HistoryContext);
  const { type, secretWords } = history.params;
  const [heartWords, setHeartWords] = useState(
    type === 'secret' ? secretWords || '' : config.defaultHeartWords
  );
  const [wordsOut, setWordsOut] = useState(false);
  const [exitToggle, setExitToggle] = useState(false);

  const fadeOut = async () => {
    setExitToggle(true);
    await waitFor(config.fadeDuration);
  };

  const prePage = async () => {
    if (exitToggle) return;
    await fadeOut();
    history.back();
    if (history.params.type === 'secret') history.back();
  };

  const [loading, setLoading] = useState(false);
  const getHeart = async () => {
    if (loading) return;
    try {
      setLoading(true);
      setHeartWords('loading...');
      const res = await fetch(config.request.readBox);
      const json = await res.json();
      setWordsOut(true);
      await waitFor(config.wordsFadeDuration);
      setHeartWords(json.data.box_message);
      setWordsOut(false);
      await waitFor(config.wordsFadeDuration);
    } catch (e) {
      alert(config.text.networkError);
      console.error(e);
      setLoading(false);
    }
    setLoading(false);
  };
  return (
    <Fade
      from={0}
      to={exitToggle ? 0.0 : 1.0}
      className="cntr full-screen"
      style={{
        alignItems: 'center'
      }}
      duration={config.fadeDuration}
    >
      <ContentBox
        style={{
          width: '70%',
          height: '30%'
        }}
      >
        <Fade
          from={1.0}
          to={wordsOut ? 0.0 : 1.0}
          duration={config.wordsFadeDuration}
        >
          {heartWords}
        </Fade>
      </ContentBox>
      {type === 'secret' ? (
        <br />
      ) : (
        <img
          src={seeBtn}
          alt="让我康康"
          onClick={getHeart}
          className="img-btn mb mt"
        />
      )}
      <img src={backBtn} alt="返回" onClick={prePage} className="img-btn" />
    </Fade>
  );
};

export default Listen;
