import React, { useContext, useState } from 'react';
import { HistoryContext } from '../components/Router';
import Fade from '../components/Fade';
import { waitFor } from 'UtilAPI';
import backBtn from 'images/back-btn.png';
import config from 'config';
// 查看盒子
const Password = () => {
  const [exitToggle, setExitToggle] = useState(false);
  let password = '';
  const history = useContext(HistoryContext);
  const fadeOut = async () => {
    setExitToggle(true);
    await waitFor(config.fadeDuration);
  };

  const prePage = async () => {
    if (exitToggle) return;
    await fadeOut();
    history.back();
  };

  const { func } = history.params;
  const [loading, setLoading] = useState(false);
  const submitPassword = async () => {
    if (loading) return;
    try {
      setLoading(true);
      if (password.length === 0) {
        alert(config.text.inputPassword);
      } else if (password.length > 20) {
        alert(config.text.passwordMaxLength);
      } else if (func === 'listen') {
        // 听，输入密码，跳转到听页面
        const res = await fetch(
          `${config.request.readSecret}?box_password=${password}`
        );
        const json = await res.json();
        setLoading(false);
        if (json.code === -1) {
          alert(config.text.readFailed);
        } else if (json.code === -2) {
          alert(config.text.passwordError);
        } else {
          history.params['secretWords'] = json.data.box_message;
          await fadeOut();
          history.push(func);
        }
      } else {
        // 发布心语
        const secret = history.params['words-secret'];
        const res = await fetch(
          `${config.request.pushSecret}?box_message=${encodeURIComponent(
            secret
          )}&box_password=${encodeURIComponent(password)}`,
          {
            method: 'post'
          }
        );
        const json = await res.json();
        setLoading(false);
        if (json.code === -1) {
          alert(config.text.pushFailed);
        } else if (json.code === -2) {
          alert(config.text.passwordRepeat);
        } else {
          alert(config.text.pushSecret + password);
          await fadeOut();
          history.push('check');
        }
      }
    } catch (e) {
      // 此处不应该这么进行判断，但我懒得写了qwq
      alert(config.text.networkError);
      console.error(e);
      setLoading(false);
    }
  };
  return (
    <Fade
      from={0}
      to={exitToggle ? 0.0 : 1.0}
      className="cntr full-screen"
      style={{
        alignItems: 'center'
      }}
      duration={config.fadeDuration}
    >
      <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
        className="mb"
      >
        <input
          type="text"
          placeholder="请输入你的密码"
          className="input-password"
          maxLength={20}
          onChange={event => (password = event.target.value)}
        />
        <span
          className="ml"
          style={{
            color: 'white'
          }}
          onClick={submitPassword}
        >
          提 交
        </span>
      </div>
      <br />
      <img src={backBtn} alt="返回" onClick={prePage} className="img-btn" />
    </Fade>
  );
};

export default Password;
