import React, { useContext, useState } from 'react';
import { HistoryContext } from '../components/Router';
import Fade from '../components/Fade';
import { waitFor } from 'UtilAPI';
import ContentBox from 'components/ContentBox';
import backBtn from 'images/back-btn.png';
import submitBtn from 'images/submit.png';
import descImg from 'images/desc.png';
import descCheckImg from 'images/desc-check.png';
import config from 'config';
// 查看盒子
const Speak = props => {
  const history = useContext(HistoryContext);
  const { type } = history.params;
  const [exitToggle, setExitToggle] = useState(false);
  const { check } = props;
  const fadeOut = async () => {
    setExitToggle(true);
    await waitFor(config.fadeDuration);
  };

  const prePage = async () => {
    if (exitToggle) return;
    await fadeOut();
    if (check) {
      history.reset();
      history.params['words-' + type] = '';
      history.push('home');
    } else {
      history.back();
    }
  };

  const [loading, setLoading] = useState(false);
  const onSubmit = async () => {
    if (history.params['words-' + type].length === 0) {
      alert(config.text.inputWords);
      return;
    } else if (history.params['words-' + type].length > 500) {
      alert(config.text.wordsMaxLength);
      return;
    }
    if (loading) return;
    try {
      setLoading(true);
      if (type === 'normal') {
        // 普通盒子，直接进行写入
        const res = await fetch(
          `${config.request.pushBox}?box_message=${encodeURIComponent(
            history.params['words-' + type]
          )}`,
          {
            method: 'post'
          }
        );

        const json = await res.json();
        if (json.code === 0) {
          alert(config.text.pushBox);
          await fadeOut();
          setLoading(false);
          history.push('check');
          setExitToggle(false);
        } else if (json.code === -1) {
          setLoading(false);
          alert(config.text.pushFailed);
        }
      } else {
        // 密码盒子，进入输入密码页面
        await fadeOut();
        setLoading(false);
        history.push('password');
      }
    } catch (e) {
      alert(config.text.networkError);
      console.error(e);
      setLoading(false);
    }
  };
  return (
    <Fade
      from={0}
      to={exitToggle ? 0.0 : 1.0}
      className="cntr full-screen"
      style={{
        alignItems: 'center'
      }}
      duration={config.fadeDuration}
    >
      <div
        className="size-auto"
        style={{
          width: '100%',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          overflow: 'auto'
        }}
      >
        <img
          src={check ? descCheckImg : descImg}
          alt="说出你想对ta说的话"
          style={{ width: '80%' }}
          className="mb"
        />
        <br />
        <ContentBox
          style={{
            width: '70%',
            height: '30vh'
          }}
        >
          <textarea
            className="editor"
            maxLength="500"
            defaultValue={history.params['words-' + type] || ''}
            onChange={e => (history.params['words-' + type] = e.target.value)}
          ></textarea>
        </ContentBox>
        <br />
        <img
          src={backBtn}
          alt="返回"
          className="img-btn mt"
          onClick={prePage}
        />
      </div>

      {check ? (
        <br />
      ) : (
        <img
          src={submitBtn}
          alt="提交"
          className={`img-btn ${window.innerHeight < 1024 && 'mb'}`}
          onClick={onSubmit}
        />
      )}
    </Fade>
  );
};

export default Speak;
