import React from 'react';
import { animated, useSpring } from 'react-spring';

const Cloud = props => {
  const cloudAnim = useSpring({
    from: {
      opacity: props.start
    },
    to: async next => {
      while (1) {
        await next({
          opacity: props.end
        });
        await next({
          opacity: props.start
        });
      }
    },
    config: { duration: props.duration }
  });
  return (
    <animated.img
      src={props.cloudImg}
      alt="CloudDark"
      className="cloud"
      style={{ ...cloudAnim, ...props.style }}
    />
  );
};

export default Cloud;
