import React, { useContext, useState } from 'react';
import { HistoryContext } from '../components/Router';
import Fade from '../components/Fade';
import { waitFor } from '../UtilAPI';
import ContentBox from '../components/ContentBox';
import listenBtn from '../images/listen.png';
import speakBtn from '../images/speak.png';
import config from 'config';
// 引导页
// 逻辑：说、听、文本
const Home = () => {
  const [exitToggle, setExitToggle] = useState(false);
  const history = useContext(HistoryContext);
  const switchFunc = async func => {
    if (exitToggle) return;
    setExitToggle(true);
    await waitFor(config.fadeDuration);
    history.params["func"] = func;
    history.push('switch-box');
  };

  return (
    <Fade from={0} to={exitToggle ? 0.0 : 1.0} className="cntr full-screen" duration={config.fadeDuration}>
      <div className="size-auto cntr" style={{ alignItems: 'space-around' }}>
        <ContentBox>
          {config.text.guide}
        </ContentBox>
      </div>
      <div className="btn-cntr">
        <img
          src={speakBtn}
          alt="说"
          onClick={() => switchFunc('speak')}
          className="img-btn"
        />
        <img
          src={listenBtn}
          alt="听"
          onClick={() => switchFunc('listen')}
          className="img-btn"
        />
      </div>
    </Fade>
  );
};

export default Home;
