import React from 'react';
import { useSpring, animated } from 'react-spring';
export default function Fade(props) {
  const { from, to, duration, delay } = props;
  const anim = useSpring({
    from: { opacity: from },
    to: { opacity: to },
    delay: delay || 0,
    config: { duration: duration || 1000 }
  });
  return <animated.div {...props} style={{...props.style, ...anim}}>{props.children}</animated.div>;
}
