import React from 'react';
import { animated, useTransition } from 'react-spring';
import meteorImg from '../images/meteor.png';

const Meteor = () => {
  let count = 0;
  const spawnMeteorPos = () => {
    return {
      xyz: [
        window.innerWidth + 500 + Math.random() * 1000,
        300 + Math.random() * -600,
        ((Math.random() * -360) / window.innerWidth) * 400
      ],
      key: `meteors-${count++}`
    };
  };

  const targetMeteorPos = xyz => {
    const offset = Math.random() * 1000 + 200;
    return [0 - offset, (xyz[1] + offset) * 0.7 + (200 - xyz[2]) / 200, xyz[2]];
  };

  const meteorAnim = useTransition(
    new Array(20).fill(0).map(() => spawnMeteorPos()),
    item => item.key,
    {
      from: item => ({ xyz: item.xyz }),
      enter: item => async next => {
        while (1) {
          await next({
            xyz: targetMeteorPos(item.xyz),

            config: {
              duration: (3 * item.xyz[2] * item.xyz[2]) / 8 + 10000
            }
          });

          await next({
            xyz: spawnMeteorPos().xyz,
            config: {
              duration: 1
            }
          });
        }
      }
    }
  );
  return meteorAnim.map(({ props, key }) => {
    return (
      <animated.img
        key={key}
        style={{
          transform: props.xyz.interpolate(
            (x, y, z) =>
              `perspective(150px) translate3d(${x}px, ${y}px, ${z}px)`
          )
        }}
        src={meteorImg}
        className="meteor"
      />
    );
  });
};

export default Meteor;
