import React, { useContext, useState } from 'react';
import { HistoryContext } from '../components/Router';
import Fade from '../components/Fade';
import { waitFor } from '../UtilAPI';
import backBtn from '../images/back-btn.png';
import normalBtn from '../images/normal-btn.png';
import secretBtn from '../images/secret-btn.png';
import config from 'config';
// 选择盒子页面
// 如果是普通盒子，直接进入
// 如果是秘密盒子，听的话进入密码页面，说的话进入输入页面
const SwitchBox = () => {
  const [exitToggle, setExitToggle] = useState(false);
  const history = useContext(HistoryContext);

  const fadeOut = async () => {
    setExitToggle(true);
    await waitFor(config.fadeDuration);
  };

  const switchBox = async box => {
    if (exitToggle) return;
    await fadeOut();
    const { func } = history.params; // listen or speak
    history.params['type'] = box; // normal or secret
    if (box === 'normal') {
      // 普通盒子
      history.push(func);
    } else {
      if (func === 'listen') {
        // 如果是听，先输密码
        history.push('password');
      } else {
        history.push(func);
      }
    }
  };

  const prePage = async () => {
    if (exitToggle) return;
    await fadeOut();
    history.back();
  };
  return (
    <Fade
      from={0}
      to={exitToggle ? 0.0 : 1.0}
      className="cntr full-screen"
      style={{
        alignItems: 'center'
      }}
      duration={config.fadeDuration}
    >
      <div
        className="btn-cntr"
        style={{
          justifyContent: 'center'
        }}
      >
        <img
          src={normalBtn}
          alt="小盒子"
          onClick={() => switchBox('normal')}
          className="box-btn mr"
        />
        <img
          src={secretBtn}
          alt="神秘盒子"
          onClick={() => switchBox('secret')}
          className="box-btn ml"
        />
      </div>
      <img src={backBtn} alt="返回" onClick={prePage} className="img-btn" />
    </Fade>
  );
};

export default SwitchBox;
