import React, { useContext, useState } from 'react';
import boxImg from '../images/box.png';
import boxOpenImg from '../images/box_open.png';
import { HistoryContext } from '../components/Router';
import Fade from '../components/Fade';
import { waitFor } from '../UtilAPI';
import config from 'config';

// 首页
// 逻辑：点击盒子后，盒子打开，由黑夜变成白天，进入功能选择页面
const Index = props => {
  const history = useContext(HistoryContext);
  const [openBox, setOpenBox] = useState(false);
  const {openBoxDuration} = config;
  const clickBox = async () => {
    if (openBox) return;
    // 1.盒子打开
    setOpenBox(true);
    // 2.黑夜变到白天
    await props.onOpenBox();
    // 3.进入功能页面
    await waitFor(500 + openBoxDuration);
    history.push('home');
  };
  return (
    <Fade from={1.0} to={openBox ? 0 : 1.0} delay={openBoxDuration + 500} duration={openBoxDuration}>
      <img
        src={openBox ? boxOpenImg : boxImg}
        alt="Box"
        className="box"
        onClick={clickBox}
      />
    </Fade>
  );
};

export default Index;
