import React, { useState } from 'react';
import Meteor from 'components/Meteor';
import Cloud from 'components/Cloud';
import 'App.css';
import Index from 'pages/Index';
import Router from 'components/Router';
import { useSpring, animated } from 'react-spring';
import Fade from 'components/Fade';
import { waitFor } from 'UtilAPI';
import Home from 'pages/Home';
import SwitchBox from 'pages/SwitchBox';
import config from 'config';
import Listen from 'pages/Listen';
import Password from 'pages/Password';
import Speak from 'pages/Speak';
import { Preload } from 'react-preload';
// 图片资源
import cloudImg from 'images/cloud.png';
import cloudDarkImg from 'images/cloud_dark.png';
import starImg from 'images/star.png';
import moonImg from 'images/moon.png';
import sunImg from 'images/sun.png';
import backBtnImg from 'images/back-btn.png';
import boxOpenImg from 'images/box_open.png';
import boxImg from 'images/box.png';
import descCheckImg from 'images/desc-check.png';
import descImg from 'images/desc.png';
import listenImg from 'images/listen.png';
import meteorImg from 'images/meteor.png';
import normalBtnImg from 'images/normal-btn.png';
import secretlBtnImg from 'images/secret-btn.png';
import seeImg from 'images/see.png';
import speakImg from 'images/speak.png';
import submitImg from 'images/submit.png';

const preloadImages = [
  cloudImg,
  cloudDarkImg,
  starImg,
  moonImg,
  sunImg,
  backBtnImg,
  boxOpenImg,
  boxImg,
  descCheckImg,
  descImg,
  listenImg,
  meteorImg,
  normalBtnImg,
  secretlBtnImg,
  seeImg,
  speakImg,
  submitImg
];

const isMobile = () => {
  const userAgentInfo = navigator.userAgent;
  const Agents = [
    'Android',
    'iPhone',
    'SymbianOS',
    'Windows Phone',
    'iPad',
    'iPod'
  ];
  for (let v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      return true;
    }
  }
  return false;
};

const App = () => {
  const mobile = isMobile();
  const [openBox, setOpenBox] = useState(false);
  const { openBoxDuration } = config;
  const backgroundAnim = useSpring({
    from: { background: 'linear-gradient(0deg, #202032, #3f3e50)' },
    to: {
      background: openBox
        ? 'linear-gradient(0deg, #191a4f, #3a4887)'
        : 'linear-gradient(0deg, #202032, #3f3e50)'
    },
    config: { duration: openBoxDuration }
  });

  if (!mobile) {
    alert(config.text.isPC);
    return (
      <>
        <h1>{config.text.isPC}</h1>
        <p>{config.text.pcDesc}</p>
      </>
    );
  }

  const clouds = [
    {
      cloudImg,
      start: 1.0,
      end: 0.5,
      duration: config.cloudFadeTime,
      style: { zIndex: -998 },
      key: 'light'
    },
    {
      cloudImg: cloudDarkImg,
      start: openBox ? 0.25 : 0.1,
      end: openBox ? 1.0 : 0.3,
      duration: config.cloudFadeTime,
      style: { zIndex: -1000 },
      key: 'dark'
    }
  ];
  const pages = {
    index: (
      <Index
        onOpenBox={async () => {
          setOpenBox(true);
          await waitFor(openBoxDuration);
        }}
      />
    ),
    home: <Home />,
    'switch-box': <SwitchBox />,
    listen: <Listen />,
    speak: <Speak />,
    password: <Password />,
    check: <Speak check />
  };

  return (
    <Preload
      loadingIndicator={<div className="pre-loading">Loading...</div>}
      images={preloadImages}
      autoResolveDelay={5000}
      onError={() => {
        alert(config.text.loadAssetError);
      }}
    >
      {
        <>
          <Router config={pages} defaultPage={'index'} />
          <animated.div className="bg" style={backgroundAnim}>
            <img className="moon" src={moonImg} alt="moon" />
            <Fade
              from={0.0}
              to={openBox ? 0.8 : 0}
              duration={openBoxDuration}
              style={{ position: 'absoulte', width: '100%', height: '100%' }}
            >
              <img className="moon" src={sunImg} alt="sun" />
            </Fade>
            <Meteor />
            <img src={starImg} alt="star" className="star" />
            {clouds.map(v => (
              <Cloud {...v} />
            ))}
          </animated.div>
        </>
      }
    </Preload>
  );
};

export default App;
